@use '../../../scss/' as *;

.champion-detail{
    position: fixed;
    background-color: $black;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 102;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.5s ease, visibility 0.5s ease;

    @include flex(flex-end, flex-start);

    &.active{
        opacity: 1;
        visibility: visible;
    }

    &__content{
        width: 60%;
        padding: 2rem 0;
        position: relative;

        .name{
            font-size: 5rem;
            text-transform: uppercase;
        }

        .story {
            width: 70%;
            padding-left: 1rem;
            border-left: 2px solid $main-color;
            font-size: 1.3rem;
            font-weight: 400;
            margin: 1rem 0;
        }

        .video{
            margin: 1rem 0;
        }

        iframe{
            width: 60%;
        }
        span{
            font-size: 1.2rem;
            text-transform: uppercase;
            padding-right: 3rem;
        }
        .spotlight{
            text-transform: unset;
        }
    }

    &__close{
        width: 50px;
        position: absolute;
        right: 30px;
        top: 30px;
        cursor: pointer;
        transition: transform .5s ease;

        &:hover{
            transform: rotate(180deg);
        }

        img{
            width: 100%;
        }
    }
}