@use '../../../scss/' as *;

.trailer{
    &__content{
        @include flex(center, flex-start);

        &__img, &__info{
            position: relative;
            width: 50%;
            height: 100%;
            transition: transform .5s ease, opacity .5s ease;
            opacity: 0;
            transition-delay: 0s;
        }

        &__img{
            transform: translateX(-200px);
            img{
                height: 100%;
            }
        }

        &__info{
            transform: translateX(200px);
            
            .video{
                margin-top: 3rem;
                width: 90%;
            }
        }
    }

    &.active &__content &__content__img,
    &.active &__content &__content__info{
        transform: translateX(0);
        opacity: 1;
        transition-delay: 1s;
    }
}